import { PropositionalWorld } from "../logic/PropositionalWorld";
import { WorldPreference } from "../logic/WorldPreference";
export function getAllAssignmentsForSignature(signature) {
    return [...signature].reduce((prev, curr) => {
        return [
            ...[...prev].map(assignment => {
                return new Set([...assignment, curr]);
            }),
            ...prev,
        ];
    }, [new Set()]);
}
export function getAllWorldsForSignature(signature) {
    return getAllAssignmentsForSignature(signature).map(assignment => new PropositionalWorld(signature, assignment));
}
export function getInitialPreferenceForSignature(signature) {
    return new WorldPreference([getAllWorldsForSignature(signature)]);
}
export function infOCFToTPTP(infOCFFormula) {
    const tptp = infOCFFormula
        .replaceAll(",", "&")
        .replaceAll(";", "|")
        .replaceAll("!", "~")
        .replaceAll("Top", "(a | ~a)")
        .replaceAll("Bottom", "(a & ~a)");
    return `fof('formula', axiom, ${tptp}).`;
}
